<template>
    <el-row>
     <el-menu
      default-active="PMS"
      class="el-menu-vertical-demo"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      :collapse="Iscollapse"
      :router="true"
      >
      <el-menu-item @click="togglePage">
        <i class="el-icon-s-unfold" v-show="Iscollapse"></i>
        <i class="el-icon-s-fold" v-show="!Iscollapse"></i>
      </el-menu-item>
      <el-menu-item v-for="(item,index) in pageList"
                    :key="index"
                    :index="item.router"
                    >
        <i :class="item.icon"></i>
        <span slot="title">{{item.name}}</span>
      </el-menu-item>
    </el-menu>
  </el-row>
</template>

<script>
export default {
  name: "Aside",
  data() {
    return {
      pageList: [
        { name: "專案管理系統", showdow: "hover", router:"PMS",desc:"專案管理系統",icon:"el-icon-document",color:'background-color:#CCEEFF'},
        { name: "通用匯入系統", showdow: "hover", router:"Import", desc:"通用匯入系統",icon:"el-icon-upload2",color:''},
        { name: "指向衡量工具", showdow: "hover", router:"Point",desc:"指向績效衡量工具",icon:"el-icon-top-right",color:''},
        { name: "程式範例", showdow: "hover", router:"Sample",desc:"Demo",icon:"el-icon-top-right",color:''},
        { name: "ORTool", showdow: "hover", router:"ORTool",desc:"ORTool",icon:"el-icon-finished",color:''},
        { name: "API", showdow: "hover", router:"API", desc:"API",icon:"el-icon-connection",color:''}
      ],
      Iscollapse:false
    };
  },
  methods:{
    togglePage(){
       this.Iscollapse = this.Iscollapse == false ? this.Iscollapse = true: this.Iscollapse = false;
    }
  },
  mounted(){

  },
  components:{

  }
};
</script>

<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 500px;
}
</style>





