<template>
  <!-- <div id="app">
    <router-view/>
  </div> -->
   <el-container>
      <el-header class="header">
          <Header></Header>
      </el-header>
      <el-container>
          <!-- <el-aside width="auto">
            <Aside></Aside>
          </el-aside> -->
          <el-main>
            <router-view></router-view>
          </el-main>
      </el-container>
  </el-container>
</template>

<script>
import MainPage from './components/MainPage'
import Aside from './components/Aside'
import Header from './components/Header'
import CardList from './components/CardList'
export default {
  name: 'Layout',
  data(){
    return{
      isCollapse:false
    }
  },
  components: {
     MainPage,
     Aside,
     Header,
     CardList
  },
  mounted(){
    this.$router.push('CardList').catch(err => {});
  }
}
</script>

<style>
.header {
  background-color: #409EFF;
  color: #fff;
  line-height: 60px;
}
</style>



