<template>
  <div>
     <el-row>
        <el-dialog
          title="設定任務難易度"
          :visible.sync="dialogVisible"
          width="40%">
          <el-form label-position="left" label-width="90px">
          <el-form-item>
              <el-col :span="24">
                <el-form-item label="快速設定">
                     <el-radio-group v-model="template.picked">
                        <el-radio v-for="(item,index) in template.List" :key="index" :label="item.Tag" @change="handleTaskChange">{{item.Name}}</el-radio>
                      </el-radio-group>
                 </el-form-item>
              </el-col>
          </el-form-item>
          <el-form-item>
              <el-col :span="12">
                <el-form-item label="原點半徑">
                    <el-input-number v-model="elementRadius" :min="1" :max="20"></el-input-number>
                 </el-form-item>
              </el-col>
            </el-form-item>
            <el-form-item>
              <el-col :span="12">
                <el-form-item label="總數量">
                    <el-input-number v-model="numNodes"></el-input-number>
                 </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="個別距離">
                    <el-input-number v-model="radius"></el-input-number>
                 </el-form-item>
              </el-col>
            </el-form-item>
            <el-form-item>
              <el-col :span="6">
                <el-form-item label="起始顏色">
                      <el-color-picker v-model="color.start"></el-color-picker>
                 </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="點擊顏色">
                      <el-color-picker v-model="color.clicked"></el-color-picker>
                 </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="背景顏色">
                      <el-color-picker v-model="color.background"></el-color-picker>
                 </el-form-item>
              </el-col>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button type="danger" size="small" @click="handleReset">重設</el-button>
            <el-button type="success" size="small" @click="handleConfirm">確定</el-button>
          </span>
        </el-dialog>
     </el-row>
      <el-row type="flex" class="row-bg" justify="space-between" style="margin-top:10%">
        <el-col :span="6">

        </el-col>
        <el-col :span="6" v-show="!dialogVisible" :style="{ 'background-color': color.background }">
            <div id="canvas" style="text-align:center;"></div>
        </el-col>
        <el-col :span="6">

        </el-col>
      </el-row>
  </div>
</template>

<script>
import * as d3 from 'd3';
export default {
   name: "Sample",
   data(){
     return{
       numNodes:20,
       radius:200,
       width:10,
       color:{
          start:'#000000',
          clicked:'#FF0000',
          background:'#FFFFFF'
       },
       template: {
          List:[
              {Name:'預設',Tag:'1'},
              {Name:'簡易',Tag:'2'},
              {Name:'中等',Tag:'3'},
              {Name:'困難',Tag:'4'},
          ],
          picked:'1',
       },
       elementRadius:10,
       elementStatus:{
          id:null,
       },
       dialogVisible:false,
     }
   },
   methods:{
     createNodes(numNodes, radius){
       var nodes = [],
             width = (radius * 2) + 50,
             height = (radius * 2) + 50,
             angle,
             x,
             y,
             i;
         for (i=0; i<numNodes; i++) {
            angle = (i / (numNodes/2)) * Math.PI;
            x = (radius * Math.cos(angle)) + (width/2);
            y = (radius * Math.sin(angle)) + (width/2);
            nodes.push({'id': i, 'x': x, 'y': y});
         }
         return nodes;
     },
     //SVG外層
     createSvg(radius, callback){
        d3.selectAll('svg').remove();
        var svg = d3.select('#canvas').append('svg:svg')
                          .attr('width', (radius * 2) + 50)
                          .attr('height', (radius * 2) + 50);
        callback(svg);
     },
     //內層圈圈
     createElements(svg, nodes, elementRadius){
        var _self  = this;
        var creation= Date.now();
        var element = svg.selectAll('circle')
                                .data(nodes)
                              .enter().append('svg:circle')
                                .attr('r', elementRadius)
                                .attr('cx', function (d, i) {
                                  return d.x;
                                })
                                .attr('cy', function (d, i) {
                                  return d.y;
                                }).on('mouseover', function() {
                                   _self.circleClick(this);
                                }).attr('id',function(x){
                                   return x.id;
                                }).attr('fill',function(){
                                    return _self.color.start;
                                });
     },
     draw(){
         var _self = this;
         var nodes = _self.createNodes(_self.numNodes, _self.radius);
         _self.createSvg(_self.radius, function (svg) {
           _self.createElements(svg, nodes, _self.elementRadius);
         });
     },
     circleClick(item){
        this.elementStatus.id = item.id;
        item.style.fill = this.color.clicked;
     },
     handleConfirm(){
       this.dialogVisible = false;
       this.draw();
     },
     handleReset(){

     },
     //難易度快速變更
     handleTaskChange(level){
       switch(level){
         case 1:
           break;
         case 2:
           break;
         case 3:
           break;
         default:
           break;
       }
     }
   },
   mounted(){
      this.dialogVisible = true;
   },
   watch:{
    //  'numNodes'(){
    //    this.draw();
    //  },
    //  'radius'(){
    //    this.draw();
    //  }
   }
}
</script>



