<template>
  <el-row>
    <el-col :span="24">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" v-if="this.$route.name!='CardList'" @click="toCardlist">回主頁</el-button>
      </el-button-group>
    </el-col>
  </el-row>
</template>


<script>
export default {
  name: 'Header',
  data(){
    return{
      isCollapse:false
    }
  },
  methods:{
    toCardlist(){
      this.$router.push('CardList');
    }
  }
}
</script>



